import React from 'react';

const Lists = (props) =>{
    
    return (
        <>
        <div className="todo">
            <li>{props.text}</li>
            <i className="fa fa-times" aria-hidden="true" id="icon"
                onClick={()=>{
                props.onSelect(props.id)}
                }
             />
        </div>
        </>
    )
};

export default Lists;

