import React from 'react';
import { useState } from 'react';
import Lists from './Lists';

const Todolist = () => {
    const [inputList, setInputList] = useState("");
    const [items, setItems] = useState([]);
    
    const itemadd = (event) => {
        setInputList(event.target.value);
    };

    const newItem = (event) => {
        setItems( (olditems) => {
            return [...olditems, inputList];
        });
        setInputList("");
    };

    const deleteItem = (id) =>{
        console.log("deleted");
        setItems( (olditems) => {
            return olditems.filter((arrEle,index) => {
                return index!==id;
            });
        });
    };

    return(
        <>
          <div className="main">
              <div className="mid">
                  <h1>TO DO LIST</h1>
                  <input type="text" placeholder="Enter Task" value={inputList} onChange={itemadd}/>
                  <button onClick={newItem}>+</button>
                  <ul>
                      {items.map( (itemval,index) => {
                        return  <Lists
                         text={itemval}
                         key={index}
                         id={index}
                         onSelect={deleteItem}
                         />
                      })
                      }
                  </ul>
              </div>
          </div>
        
        </>

    );

};

export default Todolist;
